.main--about {
    margin-top: 152px;

    @include footer-dont-float ($margin-top: 152px, $margin-bottom: 20px);

    @include tablet {
        @include footer-dont-float ($margin-top: 152px, $margin-bottom: 40px)
    }
}

.main--about .collapse {
    width: 100%;

    @include tablet { 
        width: 82.4%;
        
    }

    @include desktop {
        &--to-activate {
                height: 47px;

                &--title {
                    font-size: 24px;
                }
            }
            
        &--description {
            font-size: 24px;
            line-height: 1.3em;
            padding: 36px 27px 19px 18px;
        }
    }
    
}

.collapses-about {
    display: flex;
    flex-direction: column;
    align-items: center;
}